/* General Layout */
body {
  font-family: "Comic Sans MS", sans-serif;
  background: linear-gradient(130deg, #ffd86f, #ffb4e6, #9fd5ff);
  background-size: 200% 200%;
  animation: bgShift 10s alternate infinite;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
}

@keyframes bgShift {
  0% { background-position: left top; }
  100% { background-position: right bottom; }
}

/* Game Container */
.game-container {
  width: 92vw;
  max-width: 650px;
  padding: 24px;
  background: rgba(255,255,255,0.85);
  border-radius: 22px;
  text-align: center;
  box-shadow: 0 0 35px rgba(0,0,0,0.25);
  border: 4px solid #f2d35c;
}

/* Title + Score */
.title {
  font-size: 2.3rem;
  margin-top: 0;
  text-shadow: 1px 2px rgba(0,0,0,0.2);
}

.score {
  font-size: 1.3rem;
  margin-bottom: 10px;
}

/* Character Sprite Display */
.character {
  width: 45%;
  max-width: 280px;
  image-rendering: crisp-edges;
  margin-bottom: 8px;
  mix-blend-mode: multiply; /* helps remove image background */
  transition: 0.4s ease;
}

/* Word + Clue */
.definition {
  font-size: 1.1rem;
  font-style: italic;
  margin-bottom: 15px;
}

.masked-word {
  font-size: 2.6rem;
  font-weight: bold;
  letter-spacing: 12px;
  margin: 15px 0;
  color: #3e2a1b;
}

/* Letter Bank Section */
.letter-bank {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-top: 18px;
}

/* Letter Tiles = Stone Style */
.letter-tile {
  background: #d4c0a1;
  color: #3e2a1b;
  width: 58px;
  height: 58px;
  font-size: 1.8rem;
  font-weight: bold;
  border-radius: 6px;
  border: 3px solid #6a5436;
  cursor: pointer;
  box-shadow: 0px 4px #897454;
  transition: 0.12s;
}

.letter-tile:active {
  transform: translateY(3px);
  box-shadow: none;
}

/* Feedback */
.feedback {
  font-size: 1.4rem;
  font-weight: bold;
  margin-top: 15px;
  min-height: 30px;
}

/* Support All Screen Sizes */
@media (max-width: 480px) {
  .masked-word {
    font-size: 2.2rem;
    letter-spacing: 8px;
  }
  .letter-tile {
    width: 46px;
    height: 46px;
    font-size: 1.5rem;
  }
  .character {
    width: 55%;
  }
}
